.graph-modal {
  background-color: rgba(0, 0, 0, 0.6);
}

.graph-modal__container {
  position: relative;
  width: 500px;
  margin: auto;
  display: block;
  background-color: var(--dark-color);
  border-radius: 10px;
  padding: 67px 56px 46px;
  cursor: default;
  display: none;
  border: 1px solid rgba(255, 255, 255, 0.2);
  // height: 607px;

  @include mobile {
    padding: 50px 40px 40px;
  }
}

.graph-modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  border: none;
  padding: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url("../img/cross.svg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 20px;
  cursor: pointer;
}

.popup {
  &__title {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 28px;
    text-align: center;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 2.4em;

    @include small-tablet {
      font-size: 22px;
    }
  }

  &__paragraph {
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    color: var(--light-color);
    margin: 0;

    @include small-tablet {
      font-size: 14px;
    }

    &:not(:last-of-type) {
      margin-bottom: 1em;
    }

    &:last-of-type {
      margin-bottom: 3em;
    }

    &:last-child {
      margin-bottom: 0;
    }

    & + .form__btn {
      margin-top: 0;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.form {
  &__input,
  &__text,
  &__email {
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 50px;
    width: 100%;
    height: 42px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 186%;
    color: #7e7e7e;
    padding: 8px 24px;
    // @media (max-width: 800px) {
    //   border-radius: 25px;
    //   padding: 26px 25px;
    // }
    // @media (max-width: 700px) {
    //   font-size: 14px;
    //   border-radius: 20px;
    //   padding: 15px 20px;
    // }
  }
  &__btn {
    width: 100%;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
  }
}

