.gallery {
  padding-block: 100px;

  &__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 656px 271px 423px;
    column-gap: 16px;
    grid-auto-flow: column;
    grid-template-areas:
      "item-1 item-3 item-4"
      "item-2 item-5 item-5"
      "item-2 item-6 item-7";
    width: auto;
    box-sizing: border-box;

    @include big-desktop {
      grid-template-rows: 600px 250px 380px;
    }

    @include tablet {
      grid-template-rows: 540px 220px 380px;
    }

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 480px 480px 160px 315px;
      gap: 16px;
      grid-auto-flow: column;
      grid-template-areas:
        "item-1 item-3"
        "item-2 item-4"
        "item-5 item-5"
        "item-6 item-7";
      max-width: 600px;
    }

    @include mobile {
      display: flex;
      padding: 0;
      gap: 0;
      // grid-template-columns: 1fr;
      // grid-template-rows: 420px 390px 390px 410px 270px 255px 255px;
      // gap: 22px;
      // grid-auto-flow: column;
      // grid-template-areas:
      //   "item-1"
      //   "item-3"
      //   "item-2"
      //   "item-4"
      //   "item-5"
      //   "item-6"
      //   "item-7";
      // max-width: 350px;
    }
  }

  &__item {
    @include mobile {
      padding-inline: 8px;
    }
  }
}

.gallery-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 27px;

  @include mobile {
    gap: 16px;
    justify-content: space-between;
    height: auto;
    max-height: 400px;
  }

  &__title {
    margin-bottom: 0.42em;

    @include big-desktop {
      font-size: 30px;
      // --max-image-height: 300px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__descr {
    max-width: 300px;
    font-weight: 400;
    font-size: 16px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 1.3125em;

    @include tablet {
      font-size: 14px;
    }

    @include medium-tablet {
      margin-bottom: 0.7em;
    }
  }

  &__image-wrapper {
    // max-height: 478px;
    flex: 1;
    // flex-shrink: 0;
    overflow: hidden;
    border-radius: 40px;
    object-fit: cover;

    @include tablet {
      --max-image-height: 400px;
    }

    @include medium-tablet {
      --max-image-height: 350px;
    }

    // @include mobile {
    //   --max-image-height: 300px;
    // }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-1 {
    grid-area: item-1;

    .gallery-item__image-wrapper {
      max-height: calc(478 / 478 * var(--max-image-height));

      @include medium-tablet {
        // max-height: calc(428 / 478 * var(--max-image-height));
      }
    }

    .gallery-item__image {
      scale: 1.3;
      object-position: center 66%;
    }
  }

  &-2 {
    grid-area: item-2;

    .gallery-item__title {
      margin-top: 0.86em;

      @include medium-tablet {
        margin-top: 0;
      }
    }

    .gallery-item__image-wrapper {
      max-height: calc(428 / 478 * var(--max-image-height));

      @include mobile {
        max-height: var(--max-image-height);
      }
    }
  }

  &-3 {
    flex-direction: column-reverse;
    justify-content: start;
    grid-area: item-3;

    @include medium-tablet {
      flex-direction: column;
      justify-content: space-between;
    }

    @include mobile {
      justify-content: space-between;
    }

    .gallery-item__image-wrapper {
      max-height: calc(478 / 478 * var(--max-image-height));

      @include medium-tablet {
        max-height: calc(528 / 478 * var(--max-image-height));
      }

      @include mobile {
        max-height: calc(478 / 478 * var(--max-image-height));
      }
    }

    .gallery-item__image {
      object-position: bottom;
    }
  }

  &-4 {
    grid-area: item-4;

    .gallery-item__image-wrapper {
      max-height: calc(428 / 478 * var(--max-image-height));

      @include mobile {
        max-height: var(--max-image-height);
      }
    }

    .gallery-item__image {
      object-position: center 66%;
    }
  }

  &-5 {
    flex-direction: row-reverse;
    grid-area: item-5;
    gap: 16px;

    @include mobile {
      flex-direction: column;
    }

    @include mobile {
      justify-content: space-between;
    }

    & > * {
      flex: 1;

      @include mobile {
        flex: none;
      }
    }

    .gallery-item__title {
      margin-top: 0.86em;

      @include mobile {
        margin-top: 0;
      }
    }

    .gallery-item__image-wrapper {
      max-height: calc(227 / 478 * var(--max-image-height));

      @include mobile {
        max-height: var(--max-image-height);
        flex-shrink: 1;
      }
    }

    .gallery-item__image {
      object-position: center 41%;
      scale: 1.1;
    }
  }

  &-6 {
    flex-direction: column-reverse;
    justify-content: start;
    grid-area: item-6;

    @include mobile {
      flex-direction: column;
      justify-content: space-between;
    }

    .gallery-item__image-wrapper {
      max-height: calc(227 / 478 * var(--max-image-height));

      @include mobile {
        max-height: var(--max-image-height);
      }
    }
  }

  &-7 {
    flex-direction: column-reverse;
    justify-content: start;
    grid-area: item-7;

    @include mobile {
      flex-direction: column;
      justify-content: space-between;
    }

    .gallery-item__image-wrapper {
      max-height: calc(227 / 478 * var(--max-image-height));

      @include mobile {
        max-height: var(--max-image-height);
      }
    }

    .gallery-item__image {
      object-position: right bottom;
    }
  }
}
