/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-big-desktop {
  @media (max-width: (1300px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: (1120px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin medium-tablet {
  @media (max-width: (876px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (480px)) {
    @content;
  }
}
