*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family), sans-serif;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  // min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;
  background-color: var(--dark-color);
  color: var(--light-color);
  overflow-x: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.heading {
  font-family: var(--second-family);
  font-weight: 800;
  font-size: 43px;
  line-height: 121%;
  text-transform: uppercase;
  color: var(--accent-color);
  text-wrap: balance;
  margin: 0;

  @include desktop {
    font-size: 34px;
  }

  @include tablet {
    font-size: 30px;
  }

  @include small-tablet {
    font-size: 24px;
  }

  @include mobile {
    font-size: 20px;
  }

  @include small-mobile {
    font-size: 18px;
  }
}

.label {
  position: relative;
  z-index: 1;
  width: var(--label-width);
  aspect-ratio: 23/7;
  background-image: url("../img/label.png");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  padding-block: calc(var(--label-width) / 230 * 8);
  padding-left: calc(var(--label-width) / 230 * 20);
  padding-right: calc(var(--label-width) / 230 * 5);
  transform: rotate(-15deg);
}
