.sponsors {
  padding-block: 120px 60px;

  @include small-tablet {
    padding-block: 70px 40px;
  }
  &__title {
    margin-bottom: 2.3em;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 90px;

    @include tablet {
      gap: 70px;
    }

    @include small-tablet {
      gap: 50px;
    }
  }
}

.sponsors-list {
  width: 100%;
  &__title {
    font-family: var(--second-family);
    font-style: italic;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.3);
    margin: 0;
    margin-bottom: 1.5em;

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, min(272px, 33%));
    height: 175px;
    justify-content: space-between;
    // gap: 100px;

    @include tablet {
      height: 150px;
    }

    @include small-tablet {
      height: 125px;
    }

    @include mobile {
      height: 118px;
    }

    @include small-mobile {
      display: flex;
    }
  }

  &__item {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;

    @include small-mobile {
      width: min(272px, 50%);
    }
  }

  &__image-wrapper {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    // margin: 10px;
  }

  &__image {
    // width: 100%;
    // height: 100%;
    // object-fit: contain;
    @include tablet {
      scale: 0.9;
    }

    @include small-tablet {
      scale: 0.8;
    }

    @include mobile {
      scale: 0.75;
    }
  }
}

.sponsors-list-platinum {
  .sponsors-list__wrapper {
    transition-timing-function: linear;
  }
}

.sponsors-list-media {
  .sponsors-list__wrapper {
    display: flex;
    transition-timing-function: linear;
    height: 350px;

    @include tablet {
      height: 300px;
    }

    @include small-tablet {
      height: 250px;
    }

    @include mobile {
      height: 225px;
    }
  }
}

.sponsors-list-media-temp {
  .sponsors-list__wrapper {
    display: flex;
    transition-timing-function: linear;
    height: 350px;

    @include tablet {
      height: 300px;
    }

    @include small-tablet {
      height: 250px;
    }

    @include mobile {
      height: 225px;
    }
  }
}
