.hero {
  // &::before {
  //   width: 100%;
  //   height: 810px;
  //   background-image: url("../img/bg.webp");
  // }
  position: relative;
  height: 810px;

  @include small-tablet {
    height: 700px;
  }

  @include mobile {
    height: 550px;
  }

  @include small-mobile {
    height: 450px;
  }

  &::after {
    // display: none;
    position: absolute;
    content: "";
    width: 100%;
    height: 112px;
    bottom: 0;
    left: 0;
    background: linear-gradient(0deg, #000 0%, rgba(0, 0, 0, 0) 100%);
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__image-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    width: var(--big-width);
    aspect-ratio: 928 / 443;
    transform: translate(-47%, -50%);

    @include small-tablet {
      transform: translate(-48%, -50%);
      --big-width: min(928px, calc(100vw - var(--container-offset) * 2) - 50px);
    }

    @include mobile {
      top: 55%;
      --big-width: min(928px, calc(100vw - var(--container-offset) * 2) - 30px);
    }

    @include small-mobile {
      transform: translate(-49%, -50%);
      // top: 50%;
    }
  }

  &__bg-image {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }

  &__image {
  }

  &__label {
    --label-width: calc(var(--big-width) / 928 * 230);
    position: absolute;
    width: var(--label-width);
    aspect-ratio: 230 / 72;
    background-image: url("../img/label.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
    left: -14%;
    bottom: 41%;
    transform: rotate(-15deg);
    padding-block: calc(var(--label-width) / 230 * 13);
    padding-left: calc(var(--label-width) / 230 * 28);
    // z-index: 20;

    @include desktop {
      left: -8%;
      // bottom: 50%;
    }
  }

  &__label-title,
  &__label-subtitle {
    display: block;
    font-size: calc(var(--label-width) / 230 * 20);

    font-family: var(--second-family);
    font-weight: 600;
    // font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__postscript {
    position: absolute;
    font-family: var(--third-family);
    font-weight: 700;
    font-size: calc(var(--big-width) / 928 * 70);
    text-align: center;
    color: var(--light-color);
    text-transform: uppercase;
    transform: rotate(-86deg);
    right: -11.4%;
    top: 20%;
  }

  &__line {
    --line-text-gap: 16px;

    max-width: 2200px;
    overflow: hidden;
    position: absolute;
    display: flex;
    align-items: center;
    gap: var(--line-text-gap);
    bottom: 55px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    padding-block: 5px;
    background-color: var(--dark-color);
    font-family: var(--second-family);
    font-style: italic;
    font-weight: 700;
    font-size: 18px;
    line-height: 100%;
    text-transform: uppercase;
    color: var(--light-color);
    // animation: scroll 5s linear infinite;

    @include mobile {
      font-size: 14px;
      bottom: 40px;
    }

    @include small-mobile {
      bottom: 20px;
    }
  }

  &__line-wrapper {
    display: flex;
    align-items: center;
    gap: calc(var(--line-text-gap) + 5px);
    animation: scroll 5s linear infinite;
  }

  &__line-text {
    white-space: nowrap;
    // animation: scroll 5s linear infinite;
  }

  &__line-logo {
    display: block;
    flex-shrink: 0;
    width: 100px;
    aspect-ratio: 3;
    background-image: url("../img/1x_logo.svg");
    background-size: contain;
    // animation: scroll 5s linear infinite;

    @include mobile {
      width: 75px;
    }
  }
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - var(--line-text-gap)));
  }
}
