.footer {
  padding-block: 30px;

  &__container {
    display: flex;
    display: grid;
    grid-auto-flow: column;
    align-items: center;

    @include small-tablet {
      grid-template-columns: 85px 1fr;
      grid-auto-flow: row;
    }
  }

  &__nav {
    margin-left: auto;

    .nav__list {
      gap: min(30px, 2vw);
    }

    .nav__link {
      font-size: 12px;
    }
  }

  &__copyright {
    max-width: 13em;
    font-weight: 500;
    font-size: 12px;
    color: var(--light-color);
    margin-left: min(100px, 6.9vw);
    margin-left: auto;
    margin-top: 13px;

    @include small-tablet {
      grid-column: span 2;
      margin-top: 2em;
      margin-right: auto;
      text-align: center;
    }
  }
}
