//     sass- -  root
// colors
:root {
  // base
  --font-family: "Montserrat";
  --second-family: "RFDewiExpanded";
  --third-family: "DINCondensed";
  --content-width: 1248px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  // --big-width: min(928px, calc(100vw - var(--container-offset) * 2));
  --big-width: min(928px, calc(100vw - var(--container-offset) * 2) - 80px);
  --label-width: 230px;

  // colors
  --light-color: #fff;
  --dark-color: #000000;
  --accent-color: #ff3300;

  // --bg-main-color: #f15423;
  // --bg-secondary-color: #2b60e2;

  --transition: 0.3s;

  --max-image-height: 478px;

  --silver: #A6A5A5;
  --gold: #FDB614;
  --platinum: #70CEE9;
  --stage: #FFFFFF;
  --photo: #C2EB2D;
  --bar: #4671F6;
  --lounge: #DD49F7;
  --chill: #53EB2D;
  --media: #FF7676;
  --vip: #7E46F6;
  --hookah: #3110FF;
}
